export interface ImMonkeyData {
    groceryListItems: Array<ImMonkeyData_GroceryListItems>;
    users: Array<ImMonkeyData_Users>;
    groceryItemStatus: Array<ImMonkeyData_Status>;
    distinctGroceryListItems: Array<ImMonkeyData_Lookup>;
};

export interface ImMonkeyData_Users {
    user_id: number;
    name: string;
    pin?: string;
    email?: string;
    sms?: string;
    notify?: boolean;
    banana_count?: number;
};

export interface ImMonkeyData_Status {
    statusID: number;
    status: string;
    action_new: boolean;
    action_sameuser: boolean;
    action_type: number;
    status_verb: string;
    status_active: boolean;
};

export interface ImMonkeyData_Lookup {
    item: string;
    code: string;
};

export interface ImMonkeyData_GroceryListItems {
    grocery_list_id: number;
    date: string;
    item: string;
    count: number;
    code: string;
    notes: string;
    status_id: number;
    status: string;
    status_verb: string;
    status_active: boolean;
    user_id: number;
    username: string;
};

export interface ImMonkeyData_State {
    itemNew: boolean;
    itemActionType: number;
    itemID: number;
    itemDate: string;
    itemName: string;
    itemCount: number;
    itemCode: string;
    itemNotes: string;
    itemStatusID: number;
    itemStatus: string;
    itemStatusVerb: string;
    itemStatusActive: boolean;
    itemUserID: number;
    itemUserName: string;
    itemChanged: boolean;
};

